				{
					
				},
				{
					
				}
			],
			//控制滑动效果
			theIndex:null,
			oldIndex:null,
			isStop:false,
		}
	},
	onShow() {
		//获取导航栏高度
		var _that = this
		wx.getSystemInfo({
		  success: function(res) {
			  console.log(res.statusBarHeight)
			_that.navnarwidth = res.statusBarHeight
			_that.windowHeight = res.windowHeight
		  }
		})
	},
	onLoad() {

	},
	methods: {
		upper(e){
			console.log(e)
		},
		lower(e){
			console.log(e)
		},
		deleteGoods(){
			console.log('我点击了删除')
		},
		scroll(){
			
		},
		//控制左滑删除效果-begin
		touchStart(index,event){
			//多点触控不触发
			if(event.touches.length>1){
				this.isStop = true;
				return ;
			}
			this.oldIndex = this.theIndex;
			this.theIndex = null;
			//初始坐标
			this.initXY = [event.touches[0].pageX,event.touches[0].pageY];
		},
		touchMove(index,event){
			//多点触控不触发
			if(event.touches.length>1){
				this.isStop = true;
				return ;
			}
			let moveX = event.touches[0].pageX - this.initXY[0];
			let moveY = event.touches[0].pageY - this.initXY[1];
			
			if(this.isStop||Math.abs(moveX)<5){
				return ;
			}
			if (Math.abs(moveY) > Math.abs(moveX)){
				// 竖向滑动-不触发左滑效果
				this.isStop = true;
				return;
			}
			
			if(moveX<0){
				this.theIndex = index;
				this.isStop = true;
			}else if(moveX>0){
				if(this.theIndex!=null&&this.oldIndex==this.theIndex){
					this.oldIndex = index;
					this.theIndex = null;
					this.isStop = true;
					setTimeout(()=>{
						this.oldIndex = null;
					},150)
				}
			}
		},
		touchEnd(index,$event){
			//结束禁止触发效果
			this.isStop = false;
		},
		//控制左滑删除效果-end
		
		
	},
	mounted() {
		
	}
}
